<template>
    <div id="address">
        <!-- <header>
           <div class="to1">
               <router-link to='/'>
                <i class="el-icon-arrow-left"></i>
               </router-link>
            </div>
           <div class="to2">{{this.$route.query.name}}</div>
           <div class="to3">切换城市</div>
        </header> -->
        <Header :Flagadd=true :FlagHome=false :FlagCity=false :FlagSignin=false :FlagSearch=false :FlagOrder=false :FlagMy=false :FlagAcc=false > 
            
        </Header>
        <div class="content">
           <div class="inp">
              <input type="text" class="inp1" placeholder="输入学校、商务楼、地址" v-model="msg" @keyup.enter="up()"/>
              <button class="btn" @click="up()">提交</button>
              <ul :style="{'display':bSys?'block':'none'}">
                    <li v-for="(item,index) in list" :key="index" @click="click(item)">
                        <div>{{item.name}}</div>
                        <div>{{item.address | ellipsis}}</div>
                    </li>
              </ul>
           </div>
           
           <h3>搜索历史</h3>
           <div class="con" :style="{'display':bSys?'none':'block'}">
                <div class="cont" v-for="(item,index) in list2" :key="index">
                    <div class="contt" @click="add(item)" >
                        <div>{{item.name}}</div>
                        <div>{{item.address | ellipsis}}</div>
                    </div>   
                </div>
                <div class="del" @click="del()">清空所有</div> 
           </div>
        </div>
        
    </div>
</template>

<script>
import Header from '../components/Header'
export default {
    components:{
     Header
    }, 
  name: 'HelloWorld',
    filters: {
    ellipsis (value) {
        if (!value) return ''
        if (value.length > 15) {
            return value.slice(0,15) + '...'
        }
        return value
        }
    },
  data () {
    return {
      msg: '',
      list:[],
      list2:[],
      bSys:false,
     
      
    }
  },
  created(){

  },
  methods:{
    ClickRouteHome(){
        this.$router.push('/Homepage')
    },
    up(){
        this.bSys=true;
        this.axios.get(`http://elm.cangdu.org/v1/pois?type=search&city_id=${this.$route.query.id}&keyword=${this.msg}`).then(res=>{
            console.log(res.data)
            this.list=res.data
        })
       
    },
    click(item){
        this.bSys=false;
        let i = this.list2.findIndex(v=>{ return v.name==item.name})
        if(i==-1){
            this.list2.push(item)
            localStorage.setItem('item',JSON.stringify(this.list2))
        }else{
            return
        }
        this.$router.push(`/Homepage?name=${item.name}&geohash=${item.geohash}`)
         
    },
    del(){
        this.list2=[]
        localStorage.setItem('item',JSON.stringify(this.list2))
    },
    add(item){
        this.$router.push(`/Homepage?name=${item.name}&geohash=${item.geohash}`)
    }
  },
  mounted(){
    
    //读取本地存储的方法
    if(JSON.parse(localStorage.getItem('item'))){
        var newList=JSON.parse(localStorage.getItem('item'))
        this.list2=newList
        console.log(newList)    
    }
    else{
        return
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#address{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F4F5F9;
    position: fixed;
    top: 0;
    background: #fff;
    z-index:5;
    //   header{
    //     width: 100%;
    //     line-height: 2rem;
    //     background: #3792E5;
    //     display: flex;
    //     .to1{
    //         margin-left: 0.5rem;
    //         color: white;
    //     }
    //     .to2{
    //         color: white;
    //         margin-left: 6rem;
    //     }
    //     .to3{
    //         margin-left: 4.5rem;
    //         color: white;
    //     }
    // }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        position: relative;
        margin-top: 1.8rem;
        .inp{
            width: 100%;
            height: 4.5rem;
            background: white;
            margin-top: 0.6rem;
            ul{
                width: 80%;
                margin-left:10%; 
                list-style: none;
                background: white;
                z-index: 5;
                position: absolute;
                top:3rem;
                
                li{
                    width: 95%;
                    padding-left: 5%;
                    line-height: 1.5rem;
                    border-bottom: 0.01rem solid gainsboro;
                }
            }
            .inp1{
                width: 78%;
                margin-left: 10%;
                border: none;
                border: 0.01rem solid grey;
                border-radius: 0.1rem;
                height: 1.2rem;
                outline-style: none ;
                margin-top: 0.8rem;
                padding-left: 2%;
            }
            .btn{
                width: 80%;
                margin-left: 10%;
                border: none;
                border-radius: 0.1rem;
                height: 1.2rem;
                outline-style: none ;
                margin-top: 0.4rem; 
                background: #3792E5;
                color: white;
           }
        }
        h3{
            margin-left: 2%;
        }
        .con{
            width: 100%;
            background: white;
            .cont{
                width: 100%;
                .contt{
                    width: 96%;
                    line-height: 1.4rem;
                    border-bottom: 0.01rem solid grey;
                    margin-top: 0.2rem;
                    padding-left: 2.5%;
                    div:nth-of-type(2){
                        font-size: 0.5rem;
                        color: grey;
                    }
                }
            }
            .del{
                background: white;
                line-height: 2rem;
                text-align:center;
                color: grey;
            }
        }
    }
    
}
    
</style>



